html {
  box-sizing: border-box;
}

*,
::after,
::before {
  box-sizing: inherit;
}

body {
  font-family: 'Oswald', sans-serif;
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  background-image: url("money.jpg");
  background-size: cover;
  background-position: center;
}

.container {
  flex-basis: 700px;
  margin: 20px;
  padding: 30px;
  background: #d4d2d2;
  border-radius: 20px;
}